1,182,241,1);\r\n    transition: .2s;\r\n    padding: 0;\r\n    color: rgb(247, 252, 255);\r\n    cursor: pointer;\r\n    border-radius: 25px;\r\n    font-size: 16px;\r\n    margin-left: 10px;\r\n}\r\n\r\n\r\n#btn:hover {\r\n    background-color: rgba(174,238,214,1);\r\n}\r\n\r\n.weather-wrap {\r\n    width: 100%;\r\n    height: 100%;\r\n    display: flex;\r\n    justify-content: space-evenly;\r\n    align-items: center;\r\n    margin-bottom: 50px;\r\n}\r\n\r\n.wrap.hide {\r\n    display: none;\r\n}\r\n\r\n.weather-item {\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: center;\r\n    align-items: center;\r\n    margin-bottom: 20px;\r\n}\r\n\r\n\r\n.temp {\r\n    font-size: 54px;\r\n    color: rgb(0, 22, 87);\r\n    font-weight: 700;\r\n    margin: 42px 0;\r\n}\r\n\r\n.w_icon {\r\n    width: 200px;\r\n}\r\n\r\n.city{\r\n    font-size: 32px;\r\n    font-weight: 700;\r\n    color: rgb(0, 22, 87);\r\n}\r\n\r\n\r\n.condition {\r\n    font-size: 32px;\r\n    color: rgb(0, 22, 87);\r\n}\r\n\r\n\r\n.values {\r\n    width: 100%;\r\n    font-size: 25px;\r\n    color: rgb(0, 22, 87);\r\n    border-bottom: 1px solid rgb(206, 217, 248);\r\n}\r\n\r\n.values:nth-child(2n) {\r\n    margin: 20px 0;\r\n}\r\n\r\n.values:last-child {\r\n    margin-bottom: 0;\r\n}\r\n\r\n.forecast-wrap {\r\n    width: 100%;\r\n    height: 300px;\r\n    display: flex;\r\n    justify-content: space-evenly;\r\n    align-items: center;\r\n    margin-bottom: 42px;\r\n}\r\n\r\n.forecast-item {\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: center;\r\n}\r\n\r\n.day, .forecast-temp, .icon {\r\n    text-align: center;\r\n}\r\n\r\n.icon {\r\n    margin: 25px 0;\r\n}\r\n\r\n#f_icon {\r\n    width: 80px;\r\n}\r\n\r\n.day, .forecast-temp {\r\n    color: rgb(0, 22, 87);\r\n    font-size: 32px;\r\n}\r\n\r\ninput {\r\n    height: 50px;\r\n    width: 50%;\r\n    border: none;\r\n    text-align: center;\r\n    margin: 50px 0;\r\n\r\n    border-radius: 25px;\r\n  