\n        padding: 0;\r\n        color: rgb(247, 252, 255);\r\n        margin: 18px 0;\r\n    }\r\n\r\n    .weather-wrap {\r\n        height: 100%;\r\n        margin-bottom: 0;\r\n        flex-wrap: wrap;\r\n    }\r\n\r\n    .weather-item {\r\n        width: 50%;\r\n        margin-bottom: 56px;\r\n    }\r\n\r\n    .wrap {\r\n        width: 100%;\r\n        display: flex;\r\n        align-items: center;\r\n        justify-content: space-around;\r\n    }\r\n\r\n    .city, .condition  {\r\n        font-size: 18px;\r\n        text-align: center;\r\n    }\r\n\r\n    .temp {\r\n        margin: 10px auto;\r\n        font-size: 24px;\r\n    }\r\n\r\n    .w_icon {\r\n        width: 100px;\r\n    }\r\n\r\n    .values {\r\n        font-size: 16px;\r\n        width: auto;\r\n    }\r\n\r\n    .values:nth-child(2n) {\r\n        margin: 5px;\r\n    }\r\n\r\n    .forecast-wrap {\r\n        height: auto;\r\n    }\r\n\r\n    .forecast-item {\r\n        width: 25%;\r\n    }\r\n\r\n    .day, .forecast-temp {\r\n        font-size: 16px;\r\n    }\r\n\r\n    .f_icon {\r\n        width: 42px;\r\n    }\r\n\r\n    .icon {\r\n        margin: 10px;\r\n    }\r\n\r\n    input::placeholder {\r\n        font-size: 16px;\r\n    }\r\n\r\n    .favorites {\r\n        flex-direction: column;\r\n    }\r\n\r\n    .city-card {\r\n        width: 100%;\r\n        padding: 10px 0;\r\n        margin: 0 0 10px 0;\r\n    }\r\n\r\n    .city-name {\r\n        font-size: 16px;\r\n        padding: 10px;\r\n    }\r\n    .city-temp {\r\n        font-size: 24px ;\r\n    }\r\n    .city-cond {\r\n        display: none;\r\n    }\r\n    .city-delete {\r\n        opacity: 1;\r\n    }\r\n}\r\n\r\n\r\n"],"sourceRoot":""}]);
